"use client"

import type React from "react"

import { useRouter } from "next/navigation"
import { useEffect, useState } from "react"
import Link from "next/link"
import { LogOut, Menu, Package, BarChart3, Warehouse, Settings, ArrowRight, Settings2, History, User, Users, FileText } from "lucide-react"

export default function AuthenticatedLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const [user, setUser] = useState<any>(null)
  const [sidebarOpen, setSidebarOpen] = useState(true)
  const router = useRouter()

  useEffect(() => {
    const userData = localStorage.getItem("user")
    if (!userData) {
      router.push("/login")
    } else {
      setUser(JSON.parse(userData))
    }
  }, [router])

  const handleLogout = () => {
    localStorage.removeItem("user")
    localStorage.removeItem("userId")
    router.push("/login")
  }

  const getNavigationItems = () => {
    const dashboardHref = user?.role === "ADMIN" ? "/dashboard/admin" : 
                         user?.role === "MANAGER" ? "/dashboard/manager" : "/dashboard/staff"
    
    const baseItems = [
      { label: "Dashboard", href: dashboardHref, icon: BarChart3 },
      { label: "Products", href: "/products", icon: Package },
      { label: "Warehouses", href: "/warehouses", icon: Warehouse },
      { label: "Receipts", href: "/receipts", icon: Package },
      { label: "Deliveries", href: "/deliveries", icon: Package },
      { label: "Transfers", href: "/transfers", icon: ArrowRight },
      { label: "Adjustments", href: "/adjustments", icon: Settings2 },
      { label: "Move History", href: "/move-history", icon: History },
    ]

    // Add admin-only items
    if (user?.role === "ADMIN") {
      baseItems.push({ label: "Users", href: "/users", icon: Users })
    }

    baseItems.push(
      { label: "Reports", href: "/reports", icon: FileText },
      { label: "Profile", href: "/profile", icon: User },
      { label: "Settings", href: "/settings", icon: Settings }
    )

    return baseItems
  }

  return (
    <div className="flex h-screen bg-background">
      {/* Mobile Overlay */}
      {sidebarOpen && (
        <div 
          className="fixed inset-0 bg-black/50 z-40 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}
      
      {/* Sidebar */}
      <div
        className={`${
          sidebarOpen ? "translate-x-0" : "-translate-x-full"
        } fixed lg:relative lg:translate-x-0 z-50 w-64 lg:w-${sidebarOpen ? "64" : "20"} bg-foreground/5 border-r border-border transition-all duration-300 h-full`}
      >
        <div className="p-4 lg:p-6 border-b border-border">
          <h1 className={`font-bold text-primary ${sidebarOpen ? "text-xl" : "lg:text-xs text-xl"}`}>
            {sidebarOpen ? "StockMaster" : "SM"}
          </h1>
        </div>

        <nav className="p-2 lg:p-4 space-y-2">
          {getNavigationItems().map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className="flex items-center gap-3 px-3 lg:px-4 py-3 rounded-lg text-muted-foreground hover:bg-primary/10 hover:text-primary transition-colors"
              onClick={() => window.innerWidth < 1024 && setSidebarOpen(false)}
            >
              <item.icon className="w-5 h-5 flex-shrink-0" />
              {(sidebarOpen || window.innerWidth < 1024) && <span className="text-sm">{item.label}</span>}
            </Link>
          ))}
        </nav>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col min-w-0">
        {/* Header */}
        <div className="border-b border-border bg-background p-4 lg:p-6 flex items-center justify-between">
          <button
            onClick={() => setSidebarOpen(!sidebarOpen)}
            className="p-2 hover:bg-foreground/5 rounded-lg transition-colors"
          >
            <Menu className="w-5 h-5" />
          </button>

          <div className="flex items-center gap-2 lg:gap-4">
            <div className="text-right hidden sm:block">
              <p className="text-sm font-medium">{user?.name}</p>
              <p className="text-xs text-muted-foreground">{user?.role}</p>
            </div>
            <button
              onClick={handleLogout}
              className="p-2 hover:bg-destructive/10 text-destructive rounded-lg transition-colors"
            >
              <LogOut className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Page Content */}
        <div className="flex-1 overflow-auto p-4 lg:p-6">{children}</div>
      </div>
    </div>
  )
}
